 

// pages/page.tsx
"use client";

import { useState } from "react";
import { BudgetProvider } from "./context/BudgetContext";
import BudgetForm from "./components/BudgetForm/BudgetForm";  
import BudgetList from "./components/BudgetList/BudgetList"; 
import ChartPage from "./components/Chart/Chart"; 
import CategoryForm from "./components/Category/Category";
import { CategoryProvider } from "./context/CategoryContext";  // CategoryProvider'ı import et
import styles from "./page.module.css"; 

export default function Home() {
  const [activeComponent, setActiveComponent] = useState<"form" | "list" | "chart" | "category" | null>("form");

  return (
    <BudgetProvider>
      <CategoryProvider>  {/* CategoryProvider'ı burada sarmallıyoruz */}
        <main className={styles.container}>
          <h1 className="text-2xl font-bold mb-4">Kişisel Bütçe Uygulaması</h1>

          {/* Butonlar */}
          <div className="mb-4">
            <button 
              className={styles.button} 
              onClick={() => setActiveComponent('form')}
            >
              Bütçe Formu
            </button>
            <button 
              className={styles.button} 
              onClick={() => setActiveComponent('list')}
            >
              Bütçe Listesi
            </button>
            <button 
              className={styles.button} 
              onClick={() => setActiveComponent('category')}
            >
              Kategoriler
            </button>
            <button 
              className={styles.button} 
              onClick={() => setActiveComponent('chart')}
            >
              Grafikler
            </button>
          </div>

          {/* Seçilen component'i göster */}
          {activeComponent === 'form' && <BudgetForm />}
          {activeComponent === 'list' && <BudgetList />}
          {activeComponent === 'chart' && <ChartPage />}
          {activeComponent === 'category' && <CategoryForm />}  {/* Kategori Formu */}
        </main>
      </CategoryProvider>
    </BudgetProvider>
  );
}
