"use client";

import React, { useState } from "react";
import { useCategory } from "../../context/CategoryContext";
import styles from './CategoryForm.module.css';

const CategoryForm = () => {
  const [name, setName] = useState("");
  const [budget, setBudget] = useState<number>(0);
  const [editIndex, setEditIndex] = useState<number | null>(null);
  const { categories, addCategory } = useCategory();

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (editIndex !== null) {
      // Update the category
      const updatedCategories = [...categories];
      updatedCategories[editIndex] = { name, budget };
      localStorage.setItem('categories', JSON.stringify(updatedCategories));
      setName("");
      setBudget(0);
      setEditIndex(null);
    } else if (name && budget > 0) {
      // Add a new category
      addCategory({ name, budget });
      setName("");
      setBudget(0);
    }
  };

  const handleDelete = (index: number) => {
    const updatedCategories = categories.filter((_, i) => i !== index);
    localStorage.setItem('categories', JSON.stringify(updatedCategories));
    window.location.reload(); // Refresh the page to update state
  };

  const handleEdit = (index: number) => {
    setName(categories[index].name);
    setBudget(categories[index].budget);
    setEditIndex(index);
  };

  return (
    <div className={styles.container}>
      <h2>Kategoriler</h2>
      <form onSubmit={handleSubmit} className={styles.form}>
        <input
          type="text"
          placeholder="Kategori Adı"
          value={name}
          onChange={(e) => setName(e.target.value)}
          className={styles.input}
        />
        <input
          type="number"
          placeholder="Bütçe"
          value={budget}
          onChange={(e) => setBudget(Number(e.target.value))}
          className={styles.input}
        />
        <button type="submit" className={styles.button}>
          {editIndex !== null ? "Güncelle" : "Ekle"}
        </button>
      </form>
      <ul className={styles.list}>
        {categories.map((category, index) => (
          <li key={index} className={styles.listItem}>
            {category.name} - {category.budget}₺
            <button 
              className={styles.editButton} 
              onClick={() => handleEdit(index)}
            >
              Güncelle
            </button>
            <button 
              className={styles.deleteButton} 
              onClick={() => handleDelete(index)}
            >
              Sil
            </button>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default CategoryForm;
